@font-face {
  font-family: 'NanumBarunGothic';
  font-style: normal;
  font-weight: 400;
  src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.eot');
  src: url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.eot?#iefix') format('embedded-opentype'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.woff') format('woff'), url('//cdn.jsdelivr.net/font-nanumlight/1.0/NanumBarunGothicWeb.ttf') format('truetype');
 }
 @font-face {
  font-family: 'JalnanGothic';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_231029@1.1/JalnanGothic.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

div {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-use-select: none;
  user-select: none;
}
nav {
  display: block;
  float: right;
}
div {
  display: block;
}
article {
  display: block;
}
ol, ul {
  margin-top: 0%;
  margin-bottom: 0%;
}
.contentWrapper {
  font-family: 'NanumBarunGothic';
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100dvh;
  /* background: navy; */
}

/* ---------- My Portfolio Main ---------- */
.myPortfolio {
  display: block;
}
.myHeader {
  font-family: 'JalnanGothic';
  background-color: white;
  box-shadow: 0 1px 4px silver;
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  right: 0;
  font-size: 2.5rem;
  height: 5rem;
  /* background-color: antiquewhite; */
}

/* ---------- Header; LEE's Portfolio ---------- */
.title {
  display: inline-block;
  font-weight: 700;
  color: black;
  /* color: hsla(0, 0%, 100%, .7); */
  line-height: 5.1rem;
  cursor: pointer;
  margin-left: 1rem;
  /* background-color: pink; */
}
.mainTitle:link, .mainTitle:visited {
  text-decoration: none;
  color: black;
}
.mainTitle:hover, .mainTitle:active { 
  /* background-color:white;  */
  color: darkblue ;
}

/* ---------- Header; Navs ---------- */
.mainNav {
  display: flex;
  /* margin-top: 15px; */
  padding: 0 1rem;
  font-weight: 700;
  font-size: 1.2rem;
  color: black;
  height: 5rem;
  width: 45rem;
  /* line-height: 2rem; */
  cursor: pointer;
  /* background-color: aqua; */
  justify-content: right;
}
.meNavs {
  display: inline-block;
  /* background-color: pink; */
  width: 30rem;
  height: 80px;
  line-height: 40px;
  margin: 0 0.5rem;
  text-align: center;
 }
.mainMenu {
  display: inline-block;
  /* background-color: aquamarine; */
 }
.mainMenu:link, .mainMenu:visited {
  /* background-color:#616469;  */
  /* padding: 10px 20px; */
  margin: 20px;
  text-decoration: none;
  width:100%;
  font-size: 1.3em;
  color: black;
}
.mainMenu:hover, .mainMenu:active { 
  /* background-color:white;  */
  color: darkblue ;
}

/* ---------- Header; About Me ---------- */
.subMenu {
  position: absolute;
  top: 100%;
  right: 34.2rem;
  width: 8rem;
  background-color: #ffffff;
  border: 1px solid silver;
  padding: 14px;
  display: flex;
  flex-direction: column;
}
.subNavs {
  font-size: 1.2rem;
  text-decoration: none;
  text-align: center;
  color: black;
  /* background-color: aquamarine; */
  margin-top: 0.1rem;
  margin-bottom: 0.1rem;
}
.subHr {
  width: 100%;
  border: 1px solid silver;
}
.subNavs:hover {
  color: darkblue;
}

/* ---------- Home ---------- */
.homeWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100dvh;
  /* background: navy; */
}
#mainHomepage {
  background: aliceblue;
}
.homeArticle {
  width: 90rem;
  padding-top: 70px;
  margin: 0 auto;
  text-align: center;
}
.mainImg {
  width: 10rem;
  margin-bottom: -0.5rem;
  pointer-events: none;
}
.homePage {
  font-family: 'JalnanGothic';
  font-weight: 500;
  font-size: 6rem;
  padding-top: 1rem;
  color: black;
  /* background-color: aquamarine; */
  /* word-break: keep-all; */
}
.homeHr {
  display: block;
  width: 10rem;
  height: 0;
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 1.5rem;
  border: none;
  border-top: 0.2rem solid silver;
  color: inherit;
  opacity: 1;
}
.hello {
  display: flex;
  font-family: 'NanumBarunGothic';
  margin: 0 auto 2rem;
  font-weight: 400;
  font-size: 3rem;
  color: black;
  line-height: 1.5;
  justify-content: center;
  /* background-color: aqua; */
}
.hello1 {
  font-family: 'NanumBarunGothic';
  animation: typing 2s steps(22), blink .5s step-end infinite alternate;
  border-right: 3.5px solid;
  white-space: nowrap;
  /* white-space: pre-line; */
  overflow: hidden;
  width: auto;
  height: 3.7rem;
  /* background-color: aquamarine; */
}
@keyframes typing {
  from { width: 0; }
}
@keyframes blink {
  50% {
    border-color: transparent;
  }
}